@use "../abstracts/" as *;

.footer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding: rem(50) 0;
  gap: rem(50);
  border-top-right-radius: rem(120);
  background-color: color-neutral(very-dark-black-blue);
}

@include set-mq(min-width, get-bp(xl)) {
  .footer {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    gap: 0;
  }
}
